.hero {
    background-color: var(--background-main-color);
}

.hero-page {
    position: relative;
}


@media screen and (max-width: 767px) {


    .hero-page {
    min-height: 450px;
    background-image: url(../images/hero/dots.png);
    background-repeat: no-repeat;
    background-position: 100% 100%;
    background-size: 320px 50px;
    }

    @media screen and (min-width: 320px) and (min-device-pixel-ratio:2),
    screen and (min-width: 320px) and (min-resolution: 192dpi),
    screen and (min-width: 320px) and (min-resolution: 2dppx) {
    .hero-page {
        background-image: url(../images/hero/dots@2x.png);
    }
    }



    .hero-title {
        width: 247px;
        font-family: 'Seymour One', sans-serif;
        color: var(--text-light-color);
        font-size: 26px;
        line-height: 1.27;
        text-transform: uppercase;
        margin-bottom: 32px;
        padding-top: 50px;
    }


    .hero-text-title {
        font-size: 37px;
    }

        .focus-in-expand {
            -webkit-animation: focus-in-expand 0.8s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
            animation: focus-in-expand 0.8s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
        }
    
        @-webkit-keyframes focus-in-expand {
            0% {
                letter-spacing: -0.5em;
                -webkit-filter: blur(12px);
                filter: blur(12px);
                opacity: 0;
            }
    
            100% {
                -webkit-filter: blur(0px);
                filter: blur(0px);
                opacity: 1;
            }
        }
    
        @keyframes focus-in-expand {
            0% {
                letter-spacing: -0.5em;
                -webkit-filter: blur(12px);
                filter: blur(12px);
                opacity: 0;
            }
    
            100% {
                -webkit-filter: blur(0px);
                filter: blur(0px);
                opacity: 1;
            }
        }

    .hero-cafes {
        display: none;
    }

    .hero-button {
        display: flex;
        gap: 12px;
        align-items: center;
        margin-bottom: 19px;
    }

    .hero-btn {
        min-width: 108px;
        padding: 10px 26px 12px;
        border: 2px solid var(--btn-default-color);
        border-radius: 8px;
        background-color: transparent;
        font-weight: 600;
        font-size: 12px;
        line-height: 1.25;
        color: var(--text-main-color);
        transition: border-color var(--duration-time) var(--timing-function);
    }

    .hero-link {
        font-weight: 600;
        font-size: 12px;
        line-height: 1.25;
        color: var(--btn-default-color);
        text-decoration: underline;
    }

    .hero-info {
        display: none;
    }

    .hero-ice-cream {
        width: 177px;
        background: transparent;
    }


    .hero-bg-ice {
        position: absolute;
        bottom: 0;
        right: 60px;
        }
    }
    


@media screen and (min-width: 480px) {
    .hero {
        position: relative;
        
    }

    .hero-page {
        min-height: 458px;
        background-position: 80% 100%;

    }

    .hero-button {
        margin-bottom: 30px;
    }

}

