    }

    .hero-text-num {
        font-size: 8px;
        line-height: 1.25;
        color: var(--text-light-color);
    }

    .hero-cafes {
        // position: absolute;
        // top: 32px;
        // right: 38px;
        text-align: right;
        padding-top: 10px;
    }

    .hero-div {
        flex-direction: row;
        display: flex;
        justify-content: space-between;
        margin-bottom: 54px;
    }

    .hero-food-court {
        margin-bottom: 20px;
    }

    .hero-button {
        display: flex;
        gap: 12px;
        align-items: center;
        margin-bottom: 0;
        // margin-top: 12px;
        
    }
        
    .hero-btn {
        min-width: 108px;
        max-width: 200px;
        padding: 9px 26px 13px;
        height: 38px;
        border: 2px solid var(--btn-default-color);
        border-radius: 8px;
        background-color: transparent;
        font-weight: 600;
        font-size: 12px;
        line-height: 1.25;
        padding: 11px 26px;
        transition: border-color var(--duration-time) var(--timing-function);
        color: var(--text-main-color);
    }

        .hero-btn:hover {
            border-color: var(--btn-hover-color);
            cursor: url("../images/cursor-hover.png"), pointer;
        }
    
        .hero-btn:focus {
            border-color: var(--btn-hover-color);
            color: var(--text-light-color);
        }

    .hero-link {
        font-weight: 600;
