        font-size: 12px;
        line-height: 1.4;
        color: var(--text-light-color);
        width: 150px;
        height: 85px;
        
    }


    .hero-info-img {
        position: relative;
        z-index: 1;
    }

    .hero-text-info {
        display: flex;
        justify-content: center;
        flex-direction: column;
        gap: 20px;
        text-align: left;
    }

    .hero-info {
        display: flex;
        gap: 32px;
    }


    .hero-bg-ice {
        position: absolute;
        bottom: 0;
        right: 92px;
    }

}

